import React, {useEffect, useState} from 'react'
import {FloatButton, Select} from "antd";
import {SyncOutlined} from "@ant-design/icons";

export default function FloatingMenu({count, setRepeatObjFn}) {
    let [repeatObj, setRepeatObj] = useState({
        toggle: false,
        repeat: {
            oneToggle: false,
            manyToggle: false,
            fromSloka: 1,
            toSloka: 5
        }
    })

    useEffect(()=>{
        setRepeatObjFn(repeatObj)
    }, [repeatObj])

    const repeatFn = buttonType => () => {
        switch (buttonType) {
            case "one":
                let temp1 = {
                    ...repeatObj,
                    repeat: {oneToggle: !repeatObj.repeat.oneToggle, manyToggle: false}
                }
                setRepeatObj({...temp1})
                break
            case "many":
                let temp2 = {
                    ...repeatObj,
                    repeat: {...repeatObj.repeat, manyToggle: !repeatObj.repeat.manyToggle, oneToggle: false}
                }
                setRepeatObj({...temp2})
                console.log(temp2)
                break
        }
    }

    const setFrom = val => {
        repeatObj.repeat.fromSloka = val
        setRepeatObj({...repeatObj})
        console.log(repeatObj)
    }
    const setTo = val => {
        repeatObj.repeat.toSloka = val
        setRepeatObj({...repeatObj})
        console.log(repeatObj)
    }

    const getArray = (length) => {
        return Array(length).fill(1).map((item, index) => ({value: index + 1, label: index + 1}))
    }

    return <>
        {repeatObj.toggle &&
            <>
                {/* ONE */}
                <FloatButton type={repeatObj?.repeat?.oneToggle ? "primary" : ""}
                             description={"1"}
                             style={{bottom: "4rem"}}
                             onClick={repeatFn("one")}/>
                {/* A-B */}
                {/*<FloatButton type={repeatObj?.repeat?.manyToggle ? "primary" : ""}*/}
                {/*             description={"A-B"}*/}
                {/*             style={{bottom: "4rem"}}*/}
                {/*             onClick={repeatFn("many")}/>*/}
                {/*<FloatButton*/}
                {/*    shape={"square"}*/}
                {/*    description={<Select style={{width: "4rem"}} defaultValue={repeatObj.repeat.fromSloka} options={getArray(count)}*/}
                {/*                         onSelect={val => setFrom(val)}/>}*/}
                {/*    style={{width: "4rem", bottom: "4rem", right: "8rem"}}/>*/}
                {/*<FloatButton*/}
                {/*    shape={"square"}*/}
                {/*    description={<Select style={{width: "4rem"}} defaultValue={repeatObj.repeat.toSloka} options={getArray(count)}*/}
                {/*                         onSelect={val => setTo(val)}/>}*/}
                {/*    style={{width: "4rem", bottom: "4rem", right: "4rem"}}/>*/}
            </>
        }
        <FloatButton icon={<SyncOutlined/>} style={{bottom: "1rem"}}
                     onClick={() => setRepeatObj({...repeatObj, toggle: !repeatObj.toggle})}/>
    </>
}
